import React from 'react'
const Pack = ({
    isEmpty,
    entryListIdx,
    poListIdx,
    wmsPoSkuInfoId,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    grade,
    title,
    className,
    packThreshold
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    setTimeout(() => {
        const focus = document.getElementById(`pack_${poListIdx}_${entryListIdx + 1}`)
        if (focus) focus.focus()
    }, 10);
    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input id={`pack_${poListIdx}_${entryListIdx}`} maxLength='10' autoComplete='off' type='text' className='w-100 plain-input' value={values} name='pack' placeholder={placeholder} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom}</div>
            </div>
        </div>
    )
}

export default Pack