import React, { useState, useEffect } from "react";
import { getPoByPoCode } from 'apiServices'
import '../SingleViewModal/index.scss'
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import SkuItemCard from './SkuItem'
import { CButton, CCol, CModal, CModalBody, CModalHeader, CRow, } from "@coreui/react";
const ItemListModal = ({ areaId, warehouseUuid, showSkuList, setShowSkuList, setLocalEntryData, setCreatePo, localEntryData, thereAreRequest, setShowScanner, moduleName }) => {
    const dimention = document.documentElement.clientHeight
    let height = dimention - 400
    const [isMobile, setIsMobile] = useState(0)
    useEffect(_ => {
        const width = document.documentElement.clientWidth

        height = width <= 500 ? height + 250 : height
        setIsMobile(height)
    }, [])


    // sku list
    const [skuList, setSkuList] = useState([])
    useEffect(_ => {

        if (showSkuList?.poNumber) getPoByPoCode({ areaId, warehouseUuid, poNumber: showSkuList?.poNumber, setSkuList, setLocalEntryData, moduleName })
    }, [showSkuList?.poNumber])

    const thereIsData = localEntryData?.jobPost?.list?.length
    return (
        <CRow className={`${showSkuList?.show ? null : 'd-none'} single-view`}>
            <CCol>
                <CModal
                    show={showSkuList?.show}
                    closeOnBackdrop={false}
                    className='w-100 bg-black'
                >
                    <CModalHeader className="px-3 pt-3  border-none LatoBold text">
                        Selected SKU
                        <label onClick={() => setShowSkuList({ show: false, poNumber: null })} className='iconU-modalClose' />
                    </CModalHeader>
                    <CModalBody className="pl-3 pr-1 py-0 w-100 overflow-y" style={{ isMobile, maxHeight: isMobile, minHeight: isMobile }}>
                        {
                            thereAreRequest ?
                                <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: isMobile }}>
                                    <img className="loading" src={loading} alt="loading" />
                                </div> :
                                (
                                    !thereIsData ? <div
                                        className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center"
                                        style={{ minHeight: isMobile }}>
                                        There is no data
                                        <div>
                                            <CButton onClick={() => {
                                                setShowScanner(true)
                                                setShowSkuList({ show: false, poNumber: null })
                                            }} className={`${showSkuList?.show ? 'bg-green text-white' : 'bg-softGreen text'} no-shadow sku-item px-5 mt-3 w-100`}>
                                                Scan another QR Code
                                            </CButton>
                                        </div>

                                    </div> :
                                        localEntryData?.jobPost?.list?.map((data, idx) => <SkuItemCard showSkuList={showSkuList} totalList={localEntryData?.jobPost?.list?.length} skuList={localEntryData} setShowSkuList={setShowSkuList} setLocalEntryData={setLocalEntryData} setCreatePo={setCreatePo} img={data?.photo} skuDescription={data?.skuName} skuNumber={data?.skuNumber} uom={data?.unitOfMeassures} uomValue={data?.totalQty} expiryDate={data?.inboundDate} idx={idx} />))
                        }
                    </CModalBody>
                    {/* <div className={`px-3 pb-3 ${showSkuList?.show}`}>
                        <CButton className={`${showSkuList?.show ? 'bg-green text-white' : 'bg-softGreen text'} no-shadow sku-item px-5 mt-3 w-100`}> Submit</CButton>
                    </div> */}
                </CModal>
            </CCol>
        </CRow>
    );
};

export default ItemListModal