import React, { useEffect } from 'react'
import SkuItem from '../SingleViewModal/SkuItem'

const SkuItemCard = ({ showSkuList, totalList, img, skuDescription, skuNumber, uom, uomValue, expiryDate, ripeness, idx, setLocalEntryData, setCreatePo, setShowSkuList, skuList }) => {

    const setEntry = async ({ skuList, setLocalEntryData, setCreatePo, skuNumber }) => {
        let selectedSku = { ...skuList }
        console.log(selectedSku);
        let list = skuList.jobPost.list.filter(data => data?.skuNumber === skuNumber)
        selectedSku.jobPost.list = list
        await setLocalEntryData(selectedSku)
        await setShowSkuList({ show: false, poNumber: null })
        await setCreatePo({ show: true, skuNumber })
    }

    useEffect(_ => {
        if (totalList === 1 && showSkuList?.show) setEntry({ skuList, setLocalEntryData, setCreatePo, skuNumber })
    }, [])

    return (
        <div className='border borderLineGrey rounded mb-3 cursor-pointer' onClick={() => setEntry({ skuList, setLocalEntryData, setCreatePo, skuNumber })}>
            <SkuItem
                img={img}
                skuDescription={skuDescription}
                skuNumber={skuNumber}
                uomLabel='weight'
                uomValue={uomValue}
                uom={uom}
                idx={idx} />
        </div>
    )
}

export default SkuItemCard