const width = document.documentElement.clientWidth

const componentConditionMovement = ({ inputType, item, category, areaMovement }) => {
    switch (inputType) {
        case 'ripeness':
            return areaMovement?.value === 3 ? null : 'd-none'
        case 'grade':
            return areaMovement?.value === 8 ? null : 'd-none'
        case 'note':
            let show = null
            if (item.ripeness?.label === 'Semi Waste' || item.ripeness?.label === 'Waste') show = null
            else show = 'd-none'

            if (areaMovement?.value === 8) {
                if (item.grade?.label === 'Semi Waste' || item.grade?.label === 'Waste') show = null
                else show = 'd-none'
            }
            else show = 'd-none'
            console.log(show, areaMovement, item.grade);
            return show
        default:
            return 'd-none'
    }
}

const componentConditionStockTake = ({ inputType, item, category }) => {
    switch (inputType) {
        case 'grade':
            return true
        case 'note':
            return item?.grade?.label === 'Waste' ? null : 'd-none'
        case 'expiryDate':
            return category === 'Fruits' || category === 'Vegetables' ? 'd-none' : null
        case 'ripeness':
            return item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : (category === 'Fruits' ? null : 'd-none')
        default:
            return 'd-none'
    }
}

const componentConditionRepack = ({ inputType, item }) => {
    switch (inputType) {
        case 'grade':
            return true
        case 'expiryDate':
            return 'd-none'
        case 'note':
            return item?.grade?.label === 'Waste' ? null : 'd-none'
        case 'excess':
            return item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : null
        case 'pack':
            return item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : null
        default:
            return 'd-none'
    }
}

export const inputController = ({ moduleName, inputType, areaMovement, item, category }) => {
    switch (moduleName) {
        case 'stockMovement':
            return componentConditionMovement({ inputType, areaMovement, item, category })
        case 'stockTake':
            return componentConditionStockTake({ inputType, item, category })
        case 'repack':
            return componentConditionRepack({ inputType })
        default:
            return 'd-none'
    }
}