import React, { useState, useEffect, useMemo } from "react";
import './index.scss'
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import { addLine, getSVSource, changeValues, submitValidation, getPoDataByHeader, setOptionValue, filterByDropdown, deleteRow, updateInitialWeight } from './services'
import { inputController } from './inputController'
import { CButton, CCol, CModal, CModalBody, CModalHeader, CRow, } from "@coreui/react";
import SkuItem from './SkuItem'
import Dropdown from 'Component/Dropdown'
import InputQty from 'Component/Input'
import InputWeight from 'Component/Input/decimal'
import Note from 'Component/Input/Note'
import { useSelector } from "react-redux";
import { isFloat } from "services";
import Trash from "assets/icons/trash.png";
import InputDatePicker from 'Component/Input/InputDatePicker'
import Excess from 'Component/Input/Excess'
import Pack from 'Component/Input/Pack'

const SingleView = ({ moduleName, areaMovement, createPo, setCreatePo, moduleEntry, setConfirmation, setSingleViewData, confirmation, setMoveAll, status }) => {
  const dimention = document.documentElement.clientHeight
  const [isMobile, setIsMobile] = useState(0)
  useEffect(_ => {
    const width = document.documentElement.clientWidth
    let height = dimention - 400
    height = width <= 500 ? height + 250 : height
    setIsMobile(height)
  }, [])

  useEffect(() => {
    if (status?.code === 500) closeModal()
  }, [status?.code]);

  const ripenessLevel = useSelector(state => state.ripenessLevel)
  const areaName = moduleEntry?.jobPost?.areaName
  const areaDestinationName = moduleEntry?.jobPost?.areaDestinationName

  const [supplierNameSelected, setSupplierNameSelected] = useState()
  const [inboundDateSelected, setInboundDateSelected] = useState()
  const [expiryDateSelected, setExpiryDateSelected] = useState()

  const [itemList, setItemList] = useState([])

  const title = {
    movement: (
      <p className="text mb-2 LatoBold">
        {areaMovement?.label}
      </p>
    ),
    stockTake: (
      <p className="text mb-2 LatoBold">
        {areaName}
      </p>
    ),
    default: (
      <p className="text mb-2">
        <span>{`Are you sure want to ${areaName == 'Waste' ? 'update' : 'send'} this Item `}</span>
        <span className={` ${areaDestinationName ? null : 'd-none'}`}>{` from`}</span>
        <span className={`green ${areaDestinationName ? null : 'd-none'}`}>{` ${areaName} `}</span>
        <span>{moduleName == 'outbound' ? 'from' : moduleName === 'repack' ? '' : 'to'} </span>
        <span className={`green ${moduleName !== 'repack' ? null : 'd-none'}`}>{` ${areaDestinationName ? areaDestinationName : areaName}?`}</span>
      </p>
    )
  }

  const { supplierName, inboundDateList, expiryDateList, module, category, uom, gradeList, totalWeight, parameterList } = getSVSource({ moduleEntry, skuNumber: createPo?.skuNumber, supplierNameSelected, inboundDateSelected, expiryDateSelected })
  const [inboundDateOption, setInboundDateOption] = useState([])
  const [expiryDateOption, setExpiryDateOption] = useState([])


  const { initialWeight } = getPoDataByHeader({ moduleName, supplierNameSelected, inboundDateSelected, expiryDateSelected, moduleEntry, skuNumber: createPo?.skuNumber, setItemList })
  const [totalQty, setTotalQty] = useState(0)
  const totalWeightUpdate = initialWeight - totalQty
  const isReady = module?.jobPost?.list?.length ? null : 'd-none'
  const isFruits = category === 'Fruits' || category === 'Vegetables' ? 'd-none' : true
  let filteredGradeList = gradeList
  const show = createPo?.show && !confirmation?.show ? true : false
  //ripeness back from staging only movement
  let rip = []
  useEffect(() => {
    if (moduleName === 'stockMovement' && ripenessLevel?.length) {
      const w = ripenessLevel.findIndex(r => r.value === 'waste')
      const sw = ripenessLevel.findIndex(r => r.value === 'semi_waste')
      if (w === -1 && areaMovement?.value === 3) ripenessLevel.push({ label: "Waste", value: "waste" })
      if (sw === -1 && areaMovement?.value === 3) ripenessLevel.push({ label: "Semi Waste", value: "semi_waste" })
    }
  }, [areaMovement])

  if (moduleName === 'stockMovement' && ripenessLevel?.length) {
    if (category !== 'Fruits') rip = ripenessLevel?.filter(r => r?.value === 'waste' || r?.value === 'semi_waste')
    if (category === 'Fruits') rip = ripenessLevel
  }

  if (moduleName === 'stockMovement' && areaMovement?.value === 8) {
    filteredGradeList = gradeList.filter(grade => grade?.label === 'Waste' || grade.label === 'Semi Waste')
  }

  // end

  useEffect(() => {
    setItemList([])
    setTotalQty(0)
    if (module?.jobPost?.list.length) {
      setOptionValue({
        module,
        moduleName,
        setTotalQty,
        setSupplierNameSelected,
        setInboundDateSelected,
        setExpiryDateSelected,
        setInboundDateOption,
        setExpiryDateOption,
        setItemList,
        setMoveAll,
        supplierName,
        inboundDateList,
        expiryDateList,
        parameterList
      })
    }
  }, [createPo?.skuNumber, moduleEntry, createPo?.show])

  function closeModal() {
    if (typeof (setCreatePo) === 'function') setCreatePo({ show: false, skuNumber: null })
  }

  function updateFilter(ddName, selected) {
    if (ddName === 'supplierName') filterByDropdown({ module, setItemList, ddName, setSupplierNameSelected, setInboundDateOption, setInboundDateSelected, setExpiryDateOption, setExpiryDateSelected, selected, parameterList, supplierNameSelected, inboundDateSelected, expiryDateSelected, moduleEntry, skuNumber: createPo?.skuNumber })
    if (ddName === 'inboundDate') filterByDropdown({ module, setItemList, ddName, setInboundDateSelected, setExpiryDateOption, setExpiryDateSelected, selected, parameterList, supplierNameSelected, inboundDateSelected, expiryDateSelected, moduleEntry, skuNumber: createPo?.skuNumber })
    if (ddName === 'expiryDate') filterByDropdown({ module, setItemList, ddName, setInboundDateSelected, setExpiryDateOption, setExpiryDateSelected, selected, parameterList, supplierNameSelected, inboundDateSelected, expiryDateSelected, moduleEntry, skuNumber: createPo?.skuNumber })
  }

  //action condition
  const addLines = moduleName === 'stockTake' ? true : (totalWeightUpdate >= 0 ? true : false)
  const submits = moduleName === 'stockTake' ? true : (itemList.length && totalWeightUpdate >= 0 ? true : false)


  useEffect(() => {
    if (moduleEntry?.jobPost?.list?.length && moduleName === 'stockTake') {
      updateInitialWeight({ moduleEntry, supplierNameSelected, inboundDateSelected, expiryDateSelected, skuNumber: createPo.skuNumber, setItemList, setTotalQty })
    }

  }, [supplierNameSelected, inboundDateSelected, expiryDateSelected]);
  return (
    <CRow onKeyDown={e => e.keyCode === 27 ? closeModal() : false} className={`${show ? null : 'd-none'} single-view`}>
      <CCol>
        <CModal
          show={show}
          closeOnBackdrop={false}
          className='w-100'
        >
          <CModalHeader className="px-3 pt-3  border-none">
            {moduleName === 'stockMovement' ? title.movement : (moduleName === 'stockTake' ? title.stockTake : title.default)}
            <label onClick={() => typeof (setCreatePo) === 'function' ? setCreatePo({ show: false, skuNumber: null }) : false} className='iconU-modalClose' />
          </CModalHeader>
          <CModalBody className="pl-3 pr-1 py-0 w-100 overflow-y" style={{ isMobile, maxHeight: isMobile, minHeight: isMobile }}>
            {
              module?.jobPost?.list?.length ? module?.jobPost?.list.map((data, idx) =>
                <SkuItem key={idx}
                  img={data?.photo}
                  skuDescription={data?.skuName}
                  skuNumber={data?.skuNumber}
                  uomLabel="weight"
                  uomValue={data?.totalQty}
                  uom={data?.unitOfMeassures}
                  check={data?.checked} />
              )

                : <div style={{ isMobile, maxHeight: isMobile, minHeight: isMobile }} className='w-100 d-flex align-items-center justify-content-center'><img className="loading" src={loading} alt="loading" /></div>
            }

            <div style={{ backgroundColor: '#F9FAFC' }} className={`w-100 p-3 mb-3 ${isReady}`}>
              <Dropdown onChangeDropdown={(selected) => updateFilter('supplierName', selected)} selectedValue={supplierNameSelected} options={supplierName} style={{}} title={'Supplier Name'} showTitle show className />
              <Dropdown onChangeDropdown={(selected) => updateFilter('inboundDate', selected)} selectedValue={inboundDateSelected} options={inboundDateOption} style={{}} title={'Inbound Date'} showTitle show className />
              <Dropdown onChangeDropdown={(selected) => updateFilter('expiryDate', selected)} selectedValue={expiryDateSelected} options={expiryDateOption} style={{}} title={'Expiry Date'} showTitle show className={isFruits} />
            </div>

            <div style={{ backgroundColor: '#F9FAFC' }} className={`w-100 p-3 ${isReady}`}>
              {
                itemList?.map((item, idx) =>
                  <div className='w-100 mb-1 d-flex'>
                    <span className='mr-1 LatoBold'>{`${idx + 1}.`}</span>
                    <span className='w-100'>
                      <div className='w-100 grid-container'>
                        <Pack
                          packThreshold={item.packThreshold}
                          isEmpty={item.packIsEmpty || item.packThresholdIsEmpty}
                          entryListIdx={idx}
                          poListIdx={0}
                          uom={'PACK'}
                          inputWidth={`w-100 ${inputController({ moduleName, inputType: 'pack', category, item })}`}
                          values={item?.pack}
                          showTitle={true}
                          title={'Input Pack'}
                          onChange={(e) => changeValues({ e, idx, itemList, setItemList, setTotalQty, module, moduleName })} />
                        <InputQty
                          entryListIdx={idx}
                          poListIdx={0}
                          isEmpty={item?.qtyIsEmpty}
                          uom={uom}
                          inputWidth={'w-100'}
                          values={item.qty}
                          onChange={(e) => changeValues({ e, idx, itemList, setItemList, setTotalQty, module, moduleName })}
                          showTitle={true}
                          title={'Input Quantity'} />
                        <InputWeight
                          entryListIdx={idx}
                          poListIdx={0}
                          isEmpty={item?.qtyIsEmpty}
                          uom={uom}
                          inputWidth={'w-100'}
                          values={item.qty}
                          onChange={(e) => changeValues({ e, idx, itemList, setItemList, setTotalQty, module, moduleName })}
                          showTitle={true}
                          title={'Input Weight'} />
                        <Dropdown
                          isEmpty={item?.ripenessIsEmpty}
                          entryListIdx={idx}
                          poListIdx={0}
                          onChangeDropdown={(selected) => changeValues({ moduleName, selected, ddName: 'ripeness', idx, itemList, setItemList })}
                          selectedValue={item?.ripeness}
                          options={rip.length ? rip : ripenessLevel}
                          style={{}}
                          title={'Ripeness Level'}
                          showTitle={true}
                          show
                          className={`${inputController({ moduleName, item, areaMovement, category, inputType: 'ripeness' })} w-100`} />
                        <Dropdown
                          entryListIdx={idx}
                          poListIdx={0}
                          isEmpty={item?.gradeIsEmpty}
                          selectedValue={item?.grade}
                          grade={item.grade?.label}
                          uom={uom}
                          show={true}
                          onChangeDropdown={(selected) => changeValues({ itemList, setItemList, idx, selected, ddName: 'grade' })}
                          title='Select Grade'
                          placeholder='Select Grade'
                          options={filteredGradeList}
                          width='w-100'
                          showTitle={true}
                          className={inputController({ moduleName, areaMovement, inputType: 'grade', category, item })} />
                        <Note
                          grade={true}
                          entryListIdx={idx}
                          poListIdx={0}
                          isEmpty={item.noteIsEmpty}
                          grade={'Waste'}
                          inputWidth={`w-100 ${inputController({ moduleName, inputType: 'note', item, areaMovement })}`}
                          values={item?.note}
                          showTitle={true}
                          title={'Note'}
                          onChange={(e) => changeValues({ e, idx, itemList, setItemList, setTotalQty })} />
                        <InputDatePicker
                          onClick={(date) => changeValues({ itemList, setItemList, idx, selected: date, ddName: 'date' })}
                          poListIdx={0}
                          entryListIdx={idx}
                          inputWidth={`w-100 ${inputController({ moduleName, inputType: 'expiryDate', category, item })}`}
                          text={item?.expiryDateText}
                          values={item?.expiryDate}
                          showTitle={true}
                          title={'Expiry Date'}
                          disabled={true} />
                        <Excess
                          entryListIdx={idx}
                          poListIdx={0}
                          packThreshold={item.packThreshold}
                          isEmpty={item.packThresholdIsEmpty}
                          inputWidth={`w-100 ${inputController({ moduleName, inputType: 'excess', category, item })}`}
                          showTitle={true}
                          title={'Excess'} />
                      </div>
                      <div className='d-flex w-100 justify-content-end align-items-center' onClick={() => deleteRow({ idx, itemList, setItemList, setTotalQty })}>
                        <img style={{ width: '15px', height: '15px' }} src={Trash} className='cursor-pointer mr-1' />
                        <span className='emptyTextField pointer'>{`Delete row`}</span>
                      </div>
                    </span>

                  </div>
                )
              }
              <div onKeyDown={(e) => e.keyCode === 13 ? addLine({ itemList, setItemList, moduleName, module }) : false} tabIndex='0' className={`w-100 sku-item ${addLines ? 'green' : 'red'} LatoBold cursor-pointer`} onClick={() => addLine({ itemList, setItemList, expiryDateSelected, moduleName, module })}>
                <i className={`iconU-entryAdd pr-2`}></i>
                {`Add ${'Weight'} (${isFloat(totalWeightUpdate)} ${true ? 'KG' : ''}${totalWeightUpdate > 1 ? "s" : ""} left)`}
              </div>
            </div>
          </CModalBody>
          <div className={`px-3 pb-3 ${isReady}`}>
            <CButton onClick={() => submits ? submitValidation({ module, moduleName, setConfirmation, setSingleViewData, itemList, setItemList, supplierNameSelected, inboundDateSelected, expiryDateSelected, areaMovement }) : false} className={`${submits ? 'bg-green text-white' : 'bg-softGreen text'} no-shadow sku-item px-5 mt-3 w-100`}> Submit</CButton>
          </div>
        </CModal>
      </CCol>
    </CRow>
  );
};

export default SingleView