import React, { useState } from "react";
import {
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CRow,
} from "@coreui/react";
// import QrReader from 'react-qr-scanner'
import QrReader from 'react-qr-reader'

const submit = ({ setShowSkuList, setShowScanner, poNumber }) => {
  setShowSkuList({ show: true, poNumber: poNumber })
  setShowScanner(false)
}

const Scanner = ({ showScanner, setShowScanner, setSearch, setShowSkuList, search, setLocalEntryData }) => {
  const width = document.documentElement.clientWidth
  let previewStyle = {
    height: 'unset',
    width: 500,
  }

  if (width <= 500) {
    previewStyle.width = 350
  }
  const [poNumber, setPoNumber] = useState(null)
  return (
    <CRow tabIndex='0' className={showScanner ? null : 'd-none'} onKeyDown={e => e.keyCode === 13 ? setShowScanner(false) : false}>
      <CCol>
        <CModal
          onClose={() => setShowScanner(false)}
          show={showScanner}
          closeOnBackdrop={true}
        >
          <CModalBody style={{ height: 'fit-content', width: previewStyle.width }} className="w-100 p-0 d-flex justify-content-center">
            <QrReader
              facingMode="environment"
              className={'rounded frame'}
              delay={0}
              style={previewStyle}
              onError={(e) => console.log(e)}
              onScan={async data => {
                if (data && showScanner) {
                  if (typeof (setLocalEntryData) === 'function') await setLocalEntryData();
                  await setSearch('barcode')
                  await setPoNumber(data)
                  await submit({ setShowSkuList, setShowScanner, poNumber: data })
                }
              }}
            />
          </CModalBody>
          {/* <CModalFooter className="px-5 border-none bg-green d-flex justify-content-between">
            <div onClick={() => setShowScanner(false)} className='LatoBold text-white pointer'>Cancel</div>
            <div className='LatoBold text-white'>{search}</div>
            <div onClick={() => submit({ setShowSkuList, setShowScanner, poNumber })} className='LatoBold text-white pointer'>Submit</div>
          </CModalFooter> */}
        </CModal>
      </CCol>
    </CRow>
  );
};

export default Scanner
