import { isFloat } from "services"

export const stockMovementValidation = ({itemList,jobPost,moduleName,areaMovement}) => {
    if(jobPost){
        let moduleIsEmpty = []
        let moduleCheckItemList = []
        let moduleTotalQty = 0
        let totalWeight = jobPost.jobPost.list[0].totalQty
        let category = jobPost.jobPost.list[0].poList[0].category
        totalWeight = isFloat(totalWeight)
        moduleCheckItemList = itemList?.map(item => {

            switch (moduleName) {
                case 'stockMovement':
                    if(item.ripeness?.value === 'waste' && !item.note){
                        item.noteIsEmpty = true
                        moduleIsEmpty.push(true)
                    }
                    if(!item.qty){
                        item.qtyIsEmpty = true
                        moduleIsEmpty.push(true)
                    }
                    else{
                        item.qtyIsEmpty = false
                        moduleIsEmpty.push(false)
                        moduleTotalQty = moduleTotalQty + isFloat(item.qty)
                    }
                    if(areaMovement?.value === 8 && !item?.grade?.value){
                        item.gradeIsEmpty = true
                        moduleIsEmpty.push(true)
                    }
                    if(areaMovement?.value === 8 && item?.grade?.value === 'waste' && !item?.note){
                        item.noteIsEmpty = true
                        moduleIsEmpty.push(true)
                    }
                    if(totalWeight < moduleTotalQty) moduleCheckItemList = moduleCheckItemList.map(item => {
                        item.qtyIsEmpty = true
                        return item
                    })
                break
                case 'stockTake':
                    if (category === 'Fruits') {
                        if ((item.qty == 0 || isNaN(item.qty)) && item.grade?.value === null) {
                            item.qtyIsEmpty = false
                            item.gradeIsEmpty = false
                            item.ripenessIsEmpty = false
                            item.noteIsEmpty = false
                        }
                        else {
                            if (item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste') {
                                isNaN(item.qty) || item.qty === "" || item.qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                                !item.grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                                item.grade?.label === 'Waste' && !item.note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                            }
                            else {
                                isNaN(item.qty) || item.qty === "" || item.qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                                !item.grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                                !item.ripeness?.value ? item.ripenessIsEmpty = true : item.ripenessIsEmpty = false
                                item.grade?.label === 'Waste' && !item.note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                            }
                        }
                    }
                    else {
                        // isNaN(qty) || qty === "" || qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
    
                        if ((item.qty == 0 || isNaN(item.qty)) && item.grade?.value === null) {
                            item.qtyIsEmpty = false
                            item.gradeIsEmpty = false
                            item.ripenessIsEmpty = false
                            item.noteIsEmpty = false
                        }
                        else {
                            isNaN(item.qty) || item.qty === "" || item.qty === null ? item.qtyIsEmpty = true : item.qtyIsEmpty = false
                            !item.grade?.value ? item.gradeIsEmpty = true : item.gradeIsEmpty = false
                            item.grade?.label === 'Waste' && !item.note ? item.noteIsEmpty = true : item.noteIsEmpty = false
                        }
                    }
                    if (item?.qtyIsEmpty || item?.gradeIsEmpty || item?.ripenessIsEmpty || item?.noteIsEmpty || item?.qtyToCompareIsEmpty) moduleIsEmpty.push(true)
                    else moduleIsEmpty.push(false)
                    break
                //
                default:
            }
            return item
        })
        return {moduleIsEmpty, moduleCheckItemList,moduleTotalQty}
    }
}