import React from 'react'
import Img from 'assets/icons/sayur-box-logo.svg'
import { isFloat } from 'services'

const SKUItem = ({
    img = Img,
    skuDescription = 'SKU Description',
    skuNumber = '1001',
    uomLabel = 'weight',
    uomValue = 100.3,
    uom = 'KG',
    isCheckedHandler = null,
    check = false,
    idx = 0,
    singleView
}) => {
    const [isChecked, setIsChecked] = React.useState(check)
    return (
        <div key={idx} className={`w-100 d-md-flex justify-content-between pl-3 pr-2 py-3 mb-0 ${isChecked ? 'bg-softGreen' : null}`}>
            <div className='d-flex align-items-center w-100 cursor-pointer'>
                <img
                    style={{ width: '50px', height: '50px' }}
                    src={img} alt='sku image'
                    className='w-5 mr-2 border softGreen rounded' />
                <div className='text-left w-100'>
                    <div className='mr-2 text LatoBold'>{skuDescription}</div>
                    <div className='w-100 d-flex justify-content-between align-items-center mt-1 m-md-0'>
                        <div className='text'>{skuNumber}</div>
                        <div className='d-flex justify-content-md-end align-items-center '>
                            <div className='mr-1 font-weight-bold green'>{isFloat(uomValue)}</div>
                            <div className='mr-2 font-weight-bold green'>{uom}</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default SKUItem